<!--变电站table-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" v-table-scroll :data="tableData"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}" :row-class-name="tableRowClassName">
            <el-table-column label="序号" type="index" width="70" fixed="left" />
            <el-table-column label="合同名称" prop="name" width="370" show-overflow-tooltip fixed="left"></el-table-column>
            <el-table-column label="甲方公司" prop="kcName" width="230" show-overflow-tooltip></el-table-column>
            <el-table-column label="乙方公司" prop="yfName" width="230" show-overflow-tooltip></el-table-column>
            <el-table-column label="用电性质" prop="wkTy" width="160" show-overflow-tooltip></el-table-column>
            <el-table-column label="合同单价" prop="price" width="110"></el-table-column>
            <el-table-column label="税率" prop="taxRate" width="90"></el-table-column>
            <el-table-column label="合同编码" prop="code" width="230" show-overflow-tooltip></el-table-column>
            <el-table-column label="运营类型" prop="devType" width="90"></el-table-column>
            <el-table-column label="操作" fixed="right" align="center" class-name="cusTable" width="220">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <span class="cus-btn line" @click="handClick('编辑',scope.row)" type="text" size="small">编辑</span>
                        <span class="cus-btn line" @click="handClick('关联查看',scope.row)" type="text"
                            size="small">关联查看</span>
                        <span class="cus-btn" @click="handClick('下载',scope.row)" type="text" size="small">下载</span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import Table from "../components/Table.vue";
    import defaultImg from "@/assets/logo1.png";
    export default {
        extends: Table,
        data() {
            return {
                defaultImg
            }
        },
        methods: {
            handleCommand(item, row) {
                this.$emit('btnClick', item, row)
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

        .btn {
            color: #2E4AF7;
            text-decoration: underline
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -15px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }

    .avatar {
        display: flex;
        align-items: center;

        img {
            width: 25px;
            height: 25px;
        }
    }
</style>