<!--合同管理-->
<template>
    <BoxTitle>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!--弹窗编辑-->
        <el-dialog :title="doType" custom-class="cusDialog" :visible.sync="this.dialogShow" width="9rem"
            :before-close="handleClose">
            <div class='model-form'>
                <form-create v-model="formObj" :rule="rule" :option="options" @kc-name-select="selectKc"
                    @fixed-price-change="fixedPriceChange" @variable-price-change="variablePriceChange">
                </form-create>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
        <!--弹窗编辑-->
        <el-dialog title="用电记录查看" custom-class="cusDialog" :visible.sync="this.dialogShowRecords" width="9rem"
            :before-close="()=>{this.dialogShowRecords = false}">
            <div class="content-title">该合同关联的用电记录如下：</div>
            <div class="item">
                <el-table v-table-scroll row-key="index" max-height="500" :data="bindRecordTableData"
                    :header-cell-style="{background:'#E6EAFE',color:'#606266'}" size="small">
                    <el-table-column label="序号" type="index" width="70" />
                    <el-table-column label="用能单位" prop="wkOrg" width="240" show-overflow-tooltip></el-table-column>
                    <el-table-column label="平台名称" prop="kpName" width="120" show-overflow-tooltip></el-table-column>
                    <el-table-column label="用电性质" prop="wkTy" width="80"></el-table-column>
                    <el-table-column label="公司名称" prop="kcName" width="240" show-overflow-tooltip></el-table-column>
                    <el-table-column label="计量点名称" prop="jpName" width="240" show-overflow-tooltip></el-table-column>
                    <el-table-column label="起码" prop="startDima" width="120"></el-table-column>
                    <el-table-column label="账单日" prop="billDay" width="70"></el-table-column>
                    <el-table-column label="开始用电时间" prop="startTime" width="170"></el-table-column>
                    <el-table-column label="结束用电时间" prop="endTime" width="170"></el-table-column>
                    <el-table-column label="运行状态" width="75">
                        <template slot-scope="scope">
                            <span
                                :class="scope.row['status']==='runing'?'green':'red'">{{scope.row['status']=='runing'?"在运":'离场'}}</span>
                        </template>

                    </el-table-column>
                    <span slot="empty">
                        该合同无用电记录，<span class='link' @click="goRouter">去这里添加</span>
                    </span>
                </el-table>
                <div class="page pagination-box">
                    <el-pagination @size-change="handleSizeChange_record" @current-change="handleCurrentChange_record"
                        :current-page="page_record.pageNum" :page-sizes="[10, 20, 50, 100]"
                        :page-size="page_record.pageSize" layout="prev, pager, next, jumper,total, sizes"
                        :total="total_record">
                    </el-pagination>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="()=>{this.dialogShowRecords = false}">关 闭</el-button>
            </span>
        </el-dialog>
    </BoxTitle>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getContractQuery,
        postContractAdd,
        postContractEdit,
        getKcListSearch,
        fileUploadUrl,
        getQueryByContract,
        resourceUrl
    } from "@/api/archives.js";
    import {
        getToken
    } from '@/utils/auth'
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        formatNum,
        getMaxFixedLen
    } from "@/utils/index.js";
    export default {
        extends: Index, //继承通用index
        name: 'kcList',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '100px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                dialogShowRecords: false, //查看绑定记录弹窗
                bindRecordTableData: [], //记录数组
                page_record: { // 分页参数
                    pageNum: 1, //当前页数
                    pageSize: 10,
                },
                total_record: 0, //分页总数
                rule: [{
                        type: 'input',
                        field: "code",
                        title: '合同编码：',
                        value: '',
                        props: {
                            placeholder: '请输入合同编码',
                        },
                        validate: [{
                            required: true,
                            message: '请输入合同编码',
                            trigger: 'blur'
                        }],
                        col: {
                            span: 12,
                        }
                    }, {
                        type: 'input',
                        field: "name",
                        title: '合同名称：',
                        value: '',
                        props: {
                            placeholder: '请输入合同名称',
                        },
                        validate: [{
                            required: true,
                            message: '请输入合同名称',
                            trigger: 'blur'
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: 'NoticeLabelInput',
                        field: "variablePrice",
                        value: 0,
                        props: {
                            title: '浮动单价(¥)',
                            placeholder: '请输入浮动单价',
                            titleWidth: '120px',
                            notice: '购电成本',
                            required: true,
                        },
                        on: {
                            change: (value) => {
                                const data = this.formObj.formData();
                                if (data.fixedPrice !== '' && data.variablePrice !== '') {
                                    let maxLen = getMaxFixedLen([Number(data.fixedPrice), Number(data
                                        .variablePrice)])
                                    this.formObj.setValue({
                                        price: formatNum(Number(data.fixedPrice) + Number(data
                                            .variablePrice), maxLen)
                                    })
                                }
                            }
                        },
                        validate: [{
                            validator: (rule, value, callback) => {
                                if (value === '') {
                                    callback(new Error('单价不能为空'));
                                } else if (value != Number(value) || value === '0.') {
                                    callback(new Error('请输入合法数字'));
                                } else {
                                    callback();
                                }
                            },
                            trigger: 'blur',
                            required: true,
                        }],
                        col: {
                            span: 8,
                        }
                    },
                    {
                        type: 'NoticeLabelInput',
                        field: "fixedPrice",
                        value: 0,
                        props: {
                            placeholder: '请输入固定单价',
                            title: '固定单价(¥)',
                            titleWidth: '120px',
                            notice: '服务费',
                        },
                        validate: [{
                            validator: (rule, value, callback) => {
                                if (value === '') {
                                    callback(new Error('单价不能为空'));
                                } else if (value != Number(value) || value === '0.') {
                                    callback(new Error('请输入合法数字'));
                                } else {
                                    callback();
                                }
                            },
                            trigger: 'blur',
                            required: true,
                        }],
                        emit: ['change'],
                        col: {
                            span: 8,
                        }
                    },

                    {
                        type: 'input',
                        field: "price",
                        title: '合同单价(¥)：',
                        value: 0,
                        props: {
                            placeholder: '自动计算',
                            disabled: true,
                        },
                        validate: [{
                            required: true,
                            trigger: 'blur'
                        }],
                        col: {
                            span: 8,
                        }
                    },
                    {
                        type: 'AutoCompleteQuery',
                        field: 'kcName',
                        title: '甲方公司：',
                        props: {
                            placeholder: '请选择公司或输入公司名称搜索',
                            fieldValue: 'fullName',
                            fieldLabel: 'fullName',
                            value: '',
                            queryFunc: (keyWord) => {
                                const opt = {
                                    pageNum: 1,
                                    pageSize: 20,
                                    _loading: false, //不显示加载中图标
                                }
                                if (keyWord !== '') {
                                    opt.keyWord = keyWord;
                                }
                                return getKcListSearch(opt).then(res => {
                                    if (res.data) {
                                        const list = res.data.map(item => ({
                                            ...item,
                                            value: item.fullName,
                                        }))
                                        return list;
                                    } else {
                                        return [];
                                    }
                                })
                            }
                        },
                        validate: [{
                            required: true,
                            message: '请选择公司',
                            trigger: 'blur'
                        }],
                        emit: ['select'],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: 'select',
                        field: "yfName",
                        title: '乙方公司：',
                        value: '成都中石油昆仑能源有限公司',
                        options: [{
                                value: "成都中石油昆仑能源有限公司",
                                label: "成都中石油昆仑能源有限公司",
                            },
                            {
                                value: "四川能投宜宾市售电有限公司",
                                label: "四川能投宜宾市售电有限公司",
                            }
                        ],
                        validate: [{
                            required: true,
                            message: '请选择运营类型',
                            trigger: 'blur'
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: 'input',
                        field: "taxRate",
                        title: '税率(%)：',
                        value: '',
                        props: {
                            placeholder: '请输入税率',
                        },
                        validate: [{
                            validator: (rule, value, callback) => {
                                if (value === '') {
                                    callback(new Error('税率不能为空'));
                                } else if (value != Number(value)) {
                                    callback(new Error('请输入合法数字'));
                                } else if (value < 0 || value >= 100) {
                                    callback(new Error('请输入正确的范围'));
                                } else {
                                    callback();
                                }
                            },
                            trigger: 'blur',
                            required: true,
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: 'select',
                        field: "devType",
                        title: '运营类型：',
                        value: '售电',
                        options: [{
                                value: "购电",
                                label: "购电",
                            },
                            {
                                value: "售电",
                                label: "售电",
                            },
                            {
                                value: "服务",
                                label: "服务",
                            },
                            {
                                value: "运维",
                                label: "运维",
                            },
                            {
                                value: "租赁",
                                label: "租赁",
                            }
                        ],
                        validate: [{
                            required: true,
                            message: '请选择运营类型',
                            trigger: 'blur'
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: 'checkbox',
                        field: "wkTy",
                        title: '用电性质：',
                        value: [],
                        options: [{
                                value: "钻井",
                                label: "钻井",
                            },
                            {
                                value: "压裂",
                                label: "压裂",
                            },
                            {
                                value: "生产",
                                label: "生产",
                            },
                            {
                                value: "试气",
                                label: "试气",
                            },
                            {
                                value: "生活",
                                label: "生活",
                            }
                        ],
                        validate: [{
                            required: true,
                            message: '请至少选择一项用电性质',
                            trigger: 'change'
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: "DatePicker",
                        field: "validDate",
                        title: "有效期限：",
                        value: new Date(),
                        props: {
                            type: "datetime",
                            format: "yyyy-MM-dd HH:mm:ss",
                            placeholder: "请选择日期",
                        },
                        validate: [{
                            required: true,
                            message: '请选择日期',
                            trigger: 'blur'
                        }],
                        col: {
                            span: 12,
                        }
                    },
                    {
                        type: "MyUpload",
                        field: "contractFile",
                        title: "合同文件 ：",
                        value: '',
                        validate: [{
                            required: true,
                            message: '请选择合同文件',
                            trigger: 'change'
                        }],
                        col: {
                            span: 12,
                        }
                    }
                ]
            }
        },
        methods: {
            //操作按钮点击事件
            btnClick(btnName, row) {
                this.currEditRow = row; // 记录当前更改的数据
                if (btnName == '关联查看') {
                    this.bindRecordTableData = [];
                    this.dialogShowRecords = true;
                    this._queryRecordById();
                } else if (btnName == '编辑') {
                    this.dialogShow = true;
                    this.doType = '编辑合同';
                    this.formObj.clearValidateState && this.formObj.clearValidateState();
                    this.$nextTick(() => {
                        const data = this.formObj.form
                        for (let key in row) {
                            data[key] = row[key];
                        }
                        if (!row.contractFile) data.contractFile = '';
                        data.wkTy = row.wkTy ? row.wkTy.split(',') : [];
                        data.taxRate = formatNum(row.taxRate * 100);
                        this.formObj.exec('kcName', 'setLabel', row.kcName);
                    })
                } else if (btnName == '下载') {
                    if (!row.contractFile) {
                        this.$message.warning('附件为空')
                    } else {
                        this.download(row.contractFile)
                    }
                }
            },
            //新增按钮
            addHand() {
                const oldType = this.currEditRow == 'new';
                this.currEditRow = 'new'; // 记录当前更改的数据
                this.doType = '新增合同';
                this.dialogShow = true;
                if (!oldType) {
                    this.$nextTick(() => {
                        const data = this.formObj.form
                        this.formObj.resetFields();
                        this.formObj.exec('kcName', 'setLabel', '')
                    })
                }
            },
            fixedPriceChange(value) {
                const data = this.formObj.formData();
                if (data.fixedPrice !== '' && data.variablePrice !== '') {
                    let maxLen = getMaxFixedLen([Number(data.fixedPrice), Number(data
                        .variablePrice)])
                    this.formObj.setValue({
                        price: formatNum(Number(data.fixedPrice) + Number(data
                            .variablePrice), maxLen)
                    })
                }
            },
            variablePriceChange(value) {
                const data = this.formObj.formData();
                if (data.fixedPrice !== '' && data.variablePrice !== '') {
                    let maxLen = getMaxFixedLen([Number(data.fixedPrice), Number(data
                        .variablePrice)])
                    this.formObj.setValue({
                        price: formatNum(Number(data.fixedPrice) + Number(data
                            .variablePrice), maxLen)
                    })
                }
            },
            fixedPriceChange(value) {
                const data = this.formObj.formData();
                if (data.fixedPrice !== '' && data.variablePrice !== '') {
                    let maxLen = getMaxFixedLen([Number(data.fixedPrice), Number(data
                        .variablePrice)])
                    this.formObj.setValue({
                        price: formatNum(Number(data.fixedPrice) + Number(data
                            .variablePrice), maxLen)
                    })
                }
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            //下载文件
            download(url) {
                const fileName = this.getShowUrl(url);
                const a = document.createElement('a');
                a.setAttribute('download', fileName);
                a.setAttribute('href', resourceUrl + "?resource=" + fileName);
                a.click();
            },
            goRouter() {
                this.dialogShowRecords = false;
                this.$router.push("/useEqManage");
            },
            //根据合同id查询用电申请绑定记录
            _queryRecordById() {
                getQueryByContract({
                    conId: this.currEditRow.id,
                    ...this.page_record
                }).then(res => {
                    this.bindRecordTableData = res.rows ? res.rows : [];
                    this.total_record = res.total;
                })
            },

            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getContractQuery({
                    ...this.search,
                    ...this.page
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //表格分页条数变化
            handleSizeChange_record(data) {
                if (this.page_record.pageSize !== data) {
                    this.page_record.pageSize = data;
                    this._getData();
                }
            },
            //表格分页页数
            handleCurrentChange_record(data) {
                if (this.page_record.pageNum !== data) {
                    this.page_record.pageNum = data;
                    this._queryRecordById();
                }
            },
            //表单提交
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        //todo 表单验证通过
                        this.loading = true;
                        if (this.currEditRow !== 'new') { //编辑
                            postContractEdit({
                                ...this.currEditRow,
                                ...data,
                                taxRate: data.taxRate / 100,
                                wkTy: data.wkTy.join(','),
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        } else { //新增
                            postContractAdd({
                                ...data,
                                taxRate: data.taxRate / 100,
                                wkTy: data.wkTy.join(','),
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                this.formObj.resetFields();
                                this.formObj.exec('kcName', 'setLabel', '')
                                return false
                            })
                        }
                    }
                })
            },
            //平台选择事件
            selectKc() {
                this.formObj.validateField('kcName', () => {})
            },
        }
    }
</script>
<style lang="less" scoped>
    .link {
        cursor: pointer;
        color: #0628f7;
        text-decoration: underline;
    }
</style>